:root {
  --background: #ffffff;
  --foreground: #171717;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  color: var(--foreground);
  background: var(--background);
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

@media (prefers-color-scheme: dark) {
  html {
    color-scheme: dark;
  }
}

header {
  padding: 1rem 2rem;
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
  background-color: #ddd;
  
}

header ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 2rem;
}

header li {
  margin: 0;
}

header a {
  color: black;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: all 0.3s ease;
}

header a:hover {
  background-color: #ddd;
  color: #21e004;
}

header div {
  font-weight: bold;
  font-size: 1.2rem;
  color: #0a0a0a;
}

.logo {
  color: #21e004;
  font-weight: bold;
  font-size: 1.5rem;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 50px;
  transition: all 0.3s ease;
  background-color: #5e5959;
}

